
@import url("https://fonts.googleapis.com/css?family=Cairo:200,300,400,600,700,900");
.submit-button {
  display: block;
  color: #fff;
  cursor: pointer;
  background-color: #0084fa;
  border: none;
  border-radius: 10px;
  font-size: 12pt;
  padding: 10px;
  min-width: 100px;
  text-align: center;
}
html {
  font-family: sans-serif;
