}
body {
  margin: 0;
  overflow-x: hidden;
  color: #5b5b5b;
}
main {
  overflow: hidden;
}
.fade-enter-active,
.fade-leave-active {
  -webkit-transition: opacity 0.2s;
  transition: opacity 0.2s;
}
.fade-enter,
.fade-leave-active {
  opacity: 0;
}
#app {
  font-family: 'Cairo', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow: hidden;
}
a {
  text-decoration: none;
  cursor: pointer;
}
svg {
  width: 20px;
  height: 20px;
}
main {
  padding: 20px;
}


